<template>
  <!--待付款-->
  <div class="pay left">
    <home-header :title="'待付款'" :back="true"></home-header>
    <div class="pay-main">
      <div class="create-item create-main-address">
        <div class="create-item-title">
          收货地址：
        </div>
        <div class="create-address">
          <div class="create-address-name">
            <p>收货人：{{info.shippingDto.receiverName}}</p>
            <p>手机：{{info.shippingDto.receiverMobile}}</p>
          </div>
          <div class="create-address-content">
            {{info.shippingDto.receiverProvince}}省{{info.shippingDto.receiverCity}}{{info.shippingDto.receiverDistrict}}{{info.shippingDto.receiverAddress}}
          </div>
        </div>
      </div>
      <div class="pay-item pay-main-pay">
        <span>支付方式</span>
        <span>易宝支付</span>
      </div>
      <div class="pay-item pay-main-message">订单附言</div>
      <p class="ReceiveWrite">{{info.remark}}</p>
      <div class="pay-item pay-main-product">
        <div class="pay-product-title">
          商品列表
        </div>
        <div class="pay-product-item">
          <div class="pay-product-list" v-for="(item,index) in info.ordersDetailList" :key="index">
            <div class="pay-product-image">
              <img :src="imgHost+item.productImage" alt="">
            </div>
            <div class="pay-product-content">
              <div class="pay-product-name ellipsis">
                {{item.productName}}
              </div>
              <div class="pay-product-count">
                数量：{{item.productAmount}}
              </div>
              <div class="pay-product-money">
                价格：<span>￥{{item.productPrice}}.00元</span>
              </div>
            </div>
          </div>
        </div>
        <div class="pay-product-collect">
          <p>商品总价：<span>￥{{info.payment}}.00元</span></p>
          <p>快递费：<span>￥元</span></p>
          <p>应付费用：<span>￥{{info.payment}}.00元</span></p>
        </div>
      </div>
      <div class="pay-main-btn">
        <span @click="submitOrder">订单提交</span>
      </div>
    </div>
    <mt-popup v-model="payPopupShow" position="bottom" class="pay-mt-popup">
      <div class="pay-popup">
        <div class="pay-title">
          <div class="pay-title-cancel" @click="payPopupShow = false">
            取消
          </div>
          确认付款
        </div>
        <div class="pay-content">
          <div class="pay-money">
            <div class="pay-money-val">￥{{info.payment}}.00</div>
            <div class="pay-money-refer">￥0</div>
          </div>
          <div class="pay-price">
            <div class="pay-price-title">优惠金额</div>
            <div class="pay-price-count">￥0</div>
          </div>
          <div class="pay-type">
            <div class="pay-type-title">付款方式</div>
            <div class="pay-type-content1" @click="bindBankCard()" v-if="bindCard==1">
              <span>您还没有绑定银行卡</span><i class="iconfont icon-arrow-right-copy"></i>
            </div>
            <div class="pay-type-content" v-if="bindCard==2" @click="addActive=true">
              <div class="pay-type-img">
                <img src="../../assets/img/ABC.png" alt="">
              </div>
              <div class="pay-type-right">
                <div class="type-right-title">
                  {{bankNum}}
                </div>
                <div class="type-right-num">
                  {{bankType}}
                </div>
              </div>
            </div>
          </div>
          <div class="pay-type-button">
            <div class="pay-type-btn" @click="rightPay()">确认支付</div>
          </div>
          <div class="pay-type-notice">
            支持银行卡支付
          </div>
        </div>
      </div>
      <!--添加银行卡-->

      <div class="pay-popup popup-add-pay" :class="addActive?'moveLeft':'moveRight'">
        <div class="pay-title">
          <div class="pay-title-cancel" @click="addActive=false">
            返回
          </div>
          选择付款方式
        </div>
        <div class="pay-content">
          <div class="pay-type">
            <div class="pay-type-content" v-for="item in bankList">
              <div class="pay-type-img">
                <img src="../../assets/img/ABC.png" alt="">
              </div>
              <div class="pay-type-right">
                <div class="type-right-title">
                  {{item.cardNo}}
                </div>
                <div class="type-right-num">
                  {{item.bankName}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="pay-add-code">
          使用新卡支付
        </div>
      </div>
    </mt-popup>

  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'

  export default {
    data() {
      return {
        bindCard:null,
        payPopupShow: false,
        addActive: false,
        bankList:[],
        bankNum:'',
        bankType:'',
        orderNo:'',
        bindId:'',
        bankCode:'',
        id:'',
        info:null,
      }
    },
    computed: {},
    components: {
      homeHeader,
    },
    watch: {
      payPopupShow: function (newVal) {
        if (!newVal) {
          this.addActive = false;
        }
      }
    },
    created(){
      this.waitPay();
    },
    methods: {
      waitPay(){
        let that=this;
        var memberId=window.localStorage.getItem('memberId')
        that.id = that.$route.query.orderNumber;
        that.ajax({
          url:'/order/showOrder',
          method:'post',
          data:{
            memberId:memberId,
            orderId:that.id
          },
          success:function(res){
            if(res.status==1000){
              that.info=res.data[0];
              console.log(that.info.shippingDto)
              console.log(that.info.ordersDetailList)
            }
          }
        })
      },
      submitOrder(){
        let that=this;
        that.payPopupShow = true;
        //银行卡列表
        that.ajax({
          url:'/pay/cardlist',
          /*url: '/pay/authUser?returnUrl=http://t.2858.com/yc-shop/api/pay/authed&userId=924',*/
          method:'post',
          success:function(res){
            console.log(res.data.code)
            var authouer=res.data.code;
            var cardList=res.data.cardList;
            console.log(authouer)
            if(authouer=='1'){
              that.bindCard=2;
              that.bankType=cardList[0].bankName;
              that.bankNum=cardList[0].cardNo;
              that.bindId=cardList[0].bindId;
              that.bankCode=cardList[0].bankCode;
              that.bankList=cardList;
            }else{
              that.bindCard=1;
            }
          }
        })
      },
      //绑定银行卡实名认证
      bindBankCard() {
        let that = this;
        that.ajax({
          url: '/pay/authUser?returnUrl=http://localhost:8080/createOrder',
          method: 'post',
          success: function (res) {
            var status = res.data.code;
            console.log(status)
            var redirectUrl = res.data.redirectUrl;
            console.log(redirectUrl)
            if (status==1) {
              window.location.href = redirectUrl;
            }
          }
        })
      },
      //调易宝支付
      rightPay() {
        let that = this;
        that.ajax({
          url: '/pay/consume?bindCardId='+that.bindId+'&bankCode='+that.bankCode+'&id='+that.orderNo+'&webCallbackUrl=http://localhost:8080/result&payment=0.1&paymentActual=0.1',
          method: 'post',
          success: function (res) {
            var code = res.data.code;
            var redirectUrl = res.data.redirectUrl;
            if (code==1) {
              window.location.href = redirectUrl;
            }
          }
        })
      }
    },
    mounted() {

    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  .pay-main {
    padding: $top 0 $bottom;
  }
  .ReceiveWrite{
    padding:0 10px;
    overflow-y: hidden;
    background:#fff;
    font-size:15px;
    line-height:22px;
    @include el(3)
  }
  //公共样式
  .pay-item {
    background: $f;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    margin: 5px 0;
    padding: 10px;
  }

  .pay-main-address {

  }
  .create-item {
    background: $f;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    margin: 5px 0;
    padding: 0 10px;
    position: relative;
    .create-item-title {
      padding: 10px 0;
    }
  }

  .create-main-address {
    .create-address {
      padding: 10px;
      @include sc($med, $gray);
      .create-address-name {
        @include fj;

      }
      .create-address-content {
        word-break: break-all;
        @include sc($min, $grey);
        padding: 5px 0 10px;
      }
    }
  }
  .pay-main-pay{
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
  }
  .pay-main-product {
    .pay-product-title {
      padding-bottom: 10px;
      border-bottom: 1px solid $border;
    }
    .pay-product-item {
      padding: 10px 0;
    }
    .pay-product-list {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      border-bottom: 1px solid $border;
      padding-bottom: 10px;
      &:last-child {
        border: 0;
      }
      .pay-product-image {
        @include wh(5rem, 5rem);
        img {
          @include wh(5rem, 5rem);
        }
      }
      .pay-product-content {
        padding-left: 10px;
        .pay-product-name {
          padding-bottom: 10px;
        }
        .pay-product-count, .pay-product-money {
          @include sc($min, $grey);
          line-height: 1.5em;
          span {
            color: $red;
          }
        }
      }
    }

  }

  .pay-product-collect {
    border-top: 1px solid $border;
    padding-top: 10px;
    p {
      text-align: right;
      @include sh($min, 1.5em);
    }
    span {
      color: $red;
    }
  }

  .pay-main-btn {
    margin: 10px;
    span {
      display: block;
      @include wh(100%, 2.6em);
      @include sc($med, $f);
      line-height: 2.6em;
      text-align: center;
      background: $red;
      border-radius: 5px;
    }
  }

  $border: #ccc;
  $size: 1rem;

  /*支付弹窗*/
  .mint-popup {
    height: 75%;
    font-family: "Microsoft Yahei";
    font-weight: 100;
    .pay-popup {
      height: 100%;
      background: #F8F8F8;
      width: 100vw;
      .pay-title {
        border-bottom: 0.5px solid $border;
        text-align: center;
        @include sh($max, 3.6rem);
        position: relative;
        .pay-title-cancel {
          position: absolute;
          left: 0;
          top: 0;
        }
      }
      .pay-content {
        padding: 0 0.5rem;
        .pay-money {
          text-align: center;
          border-bottom: 1px solid $border;
          .pay-money-title {
            @include sh(1.2rem, 2.2em);
          }
          .pay-money-val {
            @include sh(1.4rem, 2.8rem);
          }
          .pay-money-refer {
            @include sh(0.9rem, 2.6rem);
            color: #bbb;
            text-decoration: line-through;
          }
        }
        .pay-price {
          @include fj;
          flex-direction: row;
          border-bottom: 0.5px solid $border;
          @include sh($size, $min*3);
          .pay-price-count {
            color: red;
          }
        }
        .pay-type {
          .pay-type-title {
            @include sh(0.98rem, 0.98rem);
            padding-top: 1rem;
          }
          .pay-type-content1 {
            display: flex;
            display: -webkit-flex;
            justify-content: space-between;
            padding: 1rem 0.6rem 0.6rem 0;
            font-size: 18px;
            i {
              font-size: 20px;
            }
          }
          .pay-type-content {
            display: flex;
            flex-direction: row;
            padding: 1rem 0 0.6rem;
            border-bottom: 1px solid $border;
            .pay-type-img {
              padding-left: 1rem;
              @include wh(3.8rem, 2.8rem);
              img {
                @include wh(2.6rem, 2.6rem);
              }
            }
            .pay-type-right {
              display: flex;
              flex-direction: column;
              padding-left: 1.6rem;
              flex: 1;
              justify-content: space-between;
              .type-right-title, .type-right-num {
                font-size: $size;
              }
            }
          }
        }
        .pay-type-button {
          padding: 1.2rem 0.5rem 0.2rem;
          .pay-type-btn {
            @include wh(100%, 3rem);
            @include sh(1.1rem, 3rem);
            border: 0.5px solid #00a0e9;
            color: #00a0e9;
            border-radius: 5px;
            text-align: center;
            font-weight: 100;
          }
        }
        .pay-type-notice {
          text-align: center;
          @include sc($min, $gray);
        }
      }
      /*选择付款方式*/
      .pay-content {
        min-height: 30vh;
      }
      .pay-add-code {
        @include sh(1.24rem, 4rem);
        background: $f;
        text-align: center;
        font-weight: 100;
      }

    }
  }

  .pay-mt-popup {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .popup-add-pay {
      height: 100%;
      position: absolute;
      left: 100vw;
      top: 0;
    }
    .moveLeft {
      transform: translateX(-100%);
      transition: transform 0.3s;
    }
    .moveRight {
      transform: translateX(0);
      transition: transform 0.3s;
    }
  }

</style>



